* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

nav {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  padding: 15px 1%;
  padding: 20px;
  gap: 60%;
  height: 6vh;
  background-color: #292d33;
}
nav .logo {
  font-family: "Bungee Shade", sans-serif;
  font-size: 18px;
}
nav .logo a {
  color: #fff;
  text-decoration: none;
}
nav ul {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 14px;
  font-family: "Roboto";
  gap: 5vw;
  list-style-type: none;
}
nav ul li a {
  color: #fff;
  font-size: 18px;
  text-decoration: none;
}

.banner_login {
  width: 100%;
  height: 94vh;
  background-image: url("../../assets/bg_login.png");
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;
}
.banner_login .content-form {
  display: flex;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 100%;
}
.banner_login .content-form .form-login,
.banner_login .content-form .form-register {
  border: 1px solid #fff;
  width: 350px;
  background-color: rgba(225, 230, 226, 0.1);
  border-radius: 8px;
  backdrop-filter: blur(10px);
  padding: 15px 0;
}
.banner_login .content-form .form-login .profile-photo,
.banner_login .content-form .form-register .profile-photo {
  border: 1px solid #fff;
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 8% auto;
  border-radius: 50%;
  width: 90px;
  height: 90px;
  font-size: 6em;
  color: #fff;
}
.banner_login .content-form .form-login .profile-photo .photo-user,
.banner_login .content-form .form-register .profile-photo .photo-user {
  width: 100%;
  height: 100%;
  border-radius: 50%;
}
.banner_login .content-form .form-login label,
.banner_login .content-form .form-register label {
  display: block;
  text-align: center;
  position: relative;
}
.banner_login .content-form .form-login label i.bi-envelope-fill,
.banner_login .content-form .form-login label i.bi-eye,
.banner_login .content-form .form-login label i.bi-eye-slash,
.banner_login .content-form .form-login label i.bi-camera-fill,
.banner_login .content-form .form-register label i.bi-envelope-fill,
.banner_login .content-form .form-register label i.bi-eye,
.banner_login .content-form .form-register label i.bi-eye-slash,
.banner_login .content-form .form-register label i.bi-camera-fill {
  position: absolute;
  right: 42px;
  top: -4px;
  font-size: 26px;
  color: #ccc;
  cursor: pointer;
}
.banner_login .content-form .form-login label i.bi-eye-slash,
.banner_login .content-form .form-register label i.bi-eye-slash {
  display: none;
}
.banner_login .content-form .form-login input,
.banner_login .content-form .form-register input {
  outline: 0;
}
.banner_login .content-form .form-login input::placeholder,
.banner_login .content-form .form-register input::placeholder {
  color: #ccc;
}
.banner_login .content-form .form-login input[type=email],
.banner_login .content-form .form-login input[type=password],
.banner_login .content-form .form-login input[type=text],
.banner_login .content-form .form-register input[type=email],
.banner_login .content-form .form-register input[type=password],
.banner_login .content-form .form-register input[type=text] {
  width: 100%;
  max-width: 270px;
  height: 35px;
  padding-left: 5px;
  margin-bottom: 30px;
  border: none;
  border-radius: 4px;
}
.banner_login .content-form .form-login input[type=submit],
.banner_login .content-form .form-register input[type=submit] {
  border: 1px solid #fff;
  display: block;
  margin: 0 auto;
  width: 100%;
  max-width: 140px;
  padding: 5px;
  border-radius: 20px;
  background-color: transparent;
  color: #fff;
  transition: 0.5s;
}
.banner_login .content-form .form-login input[type=submit]:hover,
.banner_login .content-form .form-register input[type=submit]:hover {
  background-color: #fff;
  color: #0a0a0a;
}
.banner_login .content-form .form-login input[type=email]:focus,
.banner_login .content-form .form-login input[type=password]:focus,
.banner_login .content-form .form-login input[type=text]:focus,
.banner_login .content-form .form-register input[type=email]:focus,
.banner_login .content-form .form-register input[type=password]:focus,
.banner_login .content-form .form-register input[type=text]:focus {
  border: none;
}
.banner_login .content-form .form-login span.link-register,
.banner_login .content-form .form-login span.link-login,
.banner_login .content-form .form-register span.link-register,
.banner_login .content-form .form-register span.link-login {
  text-decoration: none;
  cursor: pointer;
  display: block;
  margin-left: 15%;
  margin-top: 14px;
  color: #fff;
}